<script>
  let width = 0;
  function handleClick() {
    width += 1;
  }
</script>

<style>
  div {
    background-color: red;
  }
</style>

<button on:click="{handleClick}">
  Clicked {width} {width === 1 ? 'time' : 'times'}
</button>
<input type="number" bind:value="{width}" />

<div bind:offsetWidth="{width}" bind:offsetHeight="{width}"></div>
